import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

//Import todo model
import { Todo } from './../../models/todo';

//Prividers
import { StorageProvider } from './../../providers/storage/storage';

@IonicPage()
@Component({
  selector: 'page-create-todo',
  templateUrl: 'create-todo.html',
})
export class CreateTodoPage {

  todo: Todo = {id: new Date().getTime(), name: "", description: "", date: new Date()};

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public storageProvider: StorageProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreateTodoPage');
  }

  createTodo() {
    this.storageProvider.saveTodo(this.todo.id, this.todo);
  }

}



